export default "import React from 'react';\nimport InputField from 'terra-form-input/lib/InputField';\n\nconst NumberInputField = () => (\n  <InputField\n    inputId=\"numeric-input\"\n    label=\"Numeric Value\"\n    type=\"number\"\n    placeholder=\"Enter Digits\"\n    inputAttrs={{\n      name: 'numeric',\n    }}\n  />\n);\n\nexport default NumberInputField;\n"